(function() {
    'use strict';

    angular
        .module('flavido')
        .factory('SignUp', SignUp);

    /** @ngInject */
    function SignUp() {
        vm.verification = {
            OTP: '',
            student_id: '',
            phone: ''
        };
        vm.student = {
            username: '',
            email_id: '',
            phone: '',
            password: ''
        };
        return {
            login: function(loginInfo) {
                $http.post(CommonInfo.getAppUrl() + "/studentlogin", vm.student).then(
                    function(response) {
                        return response;
                    },
                    function(response) {
                        return response;
                    }
                );
            },
            getInfo: function(item) {
                // if (!$localStorage.fInfoObj || !$localStorage.fInfoObj.user)
                //     $state.go('main');
                // else
                if ($localStorage && $localStorage.fInfoObj)
                    return angular.copy($localStorage.fInfoObj[item]);
                else
                    return {};
            },
            setInfo: function(item, value) {
                var obj = $localStorage.fInfoObj || {};
                obj[item] = angular.copy(value);
                $localStorage.fInfoObj = obj;
            },
            reset: function() {
                $localStorage.$reset();
            },
            getAppUrl: function() {
                return 'http://api.fundsplanner.com/v0';
            },
            showAlert: function(text) {
                $mdToast.show(
                    $mdToast.simple()
                    .textContent(text)
                    .hideDelay(3000)
                    .position('top right')
                );
            }
        };
    }
})();

(function() {
    'use strict';

    angular
        .module('flavido')
        .controller('ResetPasswordController', ResetPasswordController);

    /** @ngInject */
    function ResetPasswordController(CommonInfo, $http, growl, $state, $stateParams) {
        var vm = this;

        vm.student_email = '';
        vm.student = {
            password: '',
            confPassword: ''
        };

        vm.sendForgotPassword = sendForgotPassword;
        vm.resetPassword = resetPassword;

        activate();

        function activate() {

        }

        function sendForgotPassword() {
            if (vm.student_email) {
                $http.post(CommonInfo.getAppUrl() + "/studentresetpasswordrequest", { student_email: vm.student_email }).then(
                    function(response) {
                        if (response && response.data) {
                            if (response.data.status == 1) {
                                growl.success('Mail sent, please check your registered mail');
                            } else if (response.data.status == 2) {
                                growl.info(response.data.message);
                            }
                        } else {
                            growl.warning('There is some issue, please try after some time');
                        }
                    },
                    function(response) {
                        growl.warning('There is some issue, please try after some time');
                    }
                );
            }
        }

        function resetPassword() {
            var code = $stateParams.code;
            var caseType = $stateParams.casetype;
            if (vm.student.password && vm.student.confPassword && vm.student.password == vm.student.confPassword) {
                $http.post(CommonInfo.getAppUrl() + "/studentresetpassword", { password: vm.student.password, token: code, case_type: caseType }).then(
                    function(response) {
                        if (response && response.data) {
                            if (response.data.status == 1) {
                                growl.success('Password changed successfuly');
                                $state.go('login');
                            } else if (response.data.status == 2) {
                                growl.info(response.data.message);
                            }
                        } else {
                            growl.warning('There is some issue, please try after some time');
                        }
                    },
                    function(response) {
                        growl.warning('There is some issue, please try after some time');
                    }
                );
            }
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('flavido')
        .controller('RegisterController', RegisterController);

    /** @ngInject */
    function RegisterController($http, CommonInfo, growl, $state) {
        var vm = this;

        vm.registerStage = 1;
        vm.showOTP = false;
        vm.student = {
            username: '',
            email_id: '',
            phone: '',
            password: ''
        };
        vm.verification = {
            OTP: '',
            student_id: '',
            phone: ''
        };

        vm.signup = signup;
        vm.otpVerification = otpVerification;
        vm.sendOtp = sendOtp;

        activate();

        function activate() {

        }

        function signup() {
            if (vm.student && vm.student.username && vm.student.email_id && vm.student.password) {
                $http.post(CommonInfo.getAppUrl() + "/studentregister", vm.student).then(
                    function(response) {
                        if (response && response.data) {
                            if (response.data.status == 1 && response.data.data && response.data.data.userId) {
                                vm.registerStage = 2;
                                vm.verification.student_id = response.data.data.userId;
                            } else if (response.data.status == 2) {
                                growl.info(response.data.message);
                            }
                        } else {
                            growl.warning('There is some issue, please try after some time');
                        }
                    },
                    function(response) {
                        growl.warning('There is some issue, please try after some time');
                    }
                );
            } else {
                growl.info('Please enter all fields');
            }
        }

        function otpVerification() {
            if (vm.verification && vm.verification.phone && vm.verification.student_id && vm.verification.OTP) {
                $http.post(CommonInfo.getAppUrl() + "/verifyotp", vm.verification).then(
                    function(response) {
                        if (response && response.data) {
                            if (response.data.status == 1) {
                                growl.success('Mobile verified');
                                CommonInfo.setInfo('studentInfo', response.data.data);
                                $state.go('dashboard')
                            } else if (response.data.status == 2) {
                                growl.info(response.data.message);
                            }
                        } else {
                            growl.warning('There is some issue, please try after some time');
                        }
                    },
                    function(response) {
                        growl.warning('There is some issue, please try after some time');
                    }
                );
            }
        }

        function sendOtp() {
            if (vm.verification && vm.verification.phone && vm.verification.student_id) {
                $http.post(CommonInfo.getAppUrl() + "/verifymobileandsendotp", vm.verification ).then(
                    function(response) {
                        if (response && response.data) {
                            if (response.data.status == 1) {
                                growl.success('OTP send');
                                vm.showOTP = true;
                            } else if (response.data.status == 2) {
                                growl.info(response.data.message);
                            }
                        } else {
                            growl.warning('There is some issue, please try after some time');
                        }
                    },
                    function(response) {
                        growl.warning('There is some issue, please try after some time');
                    }
                );
            }
        }
    }
})();

