(function() {
    'use strict';

    angular
        .module('flavido')
        .factory('CommonInfo', CommonInfo)
        .directive('whenScrollEnds', whenScrollEnds)
        .directive('reviewStars', reviewStars)
        .filter('htmlToPlaintext', htmlToPlaintext)
        .filter('charCodeToChar', charCodeToChar)
        .filter('truncate', truncate)
        .filter('INR', INR);

    /** @ngInject */
    function htmlToPlaintext() {
        return function(text) {
            return text ? String(text).replace(/<[^>]+>/gm, '') : '';
        }
    }

    /** @ngInject */
    function charCodeToChar() {
        return function(code) {
            return String.fromCharCode(code);
        }
    }

    /** @ngInject */
    function truncate() {
        return function(text, char) {
            return text.length > char ? text.substr(0, char) + '...' : text;
        }
    }

    /** @ngInject */
    function INR() {
        return function(input) {
            if (!isNaN(input)) {
                var currencySymbol = '₹ ';
                //var output = Number(input).toLocaleString('en-IN');   <-- This method is not working fine in all browsers!           
                var result = input.toString().split('.');

                var lastThree = result[0].substring(result[0].length - 3);
                var otherNumbers = result[0].substring(0, result[0].length - 3);
                if (otherNumbers != '')
                    lastThree = ',' + lastThree;
                var output = otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ",") + lastThree;

                if (result.length > 1) {
                    output += "." + result[1];
                }

                return currencySymbol + output;
            }
        }
    }

    /** @ngInject */
    function whenScrollEnds($window) {
        return {
            restrict: "A",
            link: function(scope, element, attrs) {
                var visibleHeight = $window.innerHeight;
                var threshold = 100;

                element.scroll(function() {
                    var scrollableHeight = element.prop('scrollHeight');
                    var hiddenContentHeight = scrollableHeight - visibleHeight;

                    if (hiddenContentHeight - element.scrollTop() <= threshold) {
                        // Scroll is almost at the bottom. Loading more rows
                        scope.$apply(attrs.whenScrollEnds);
                    }
                });
            }
        };
    }

    /** @ngInject */
    function reviewStars(_) {
        return {
            restrict: "E",
            scope: {
                rating: '='
            },
            template: function(elem) {
                return "<span class='rating'><span class='star' ng-repeat='star in starLength track by $index' ng-class=\"{'starfull' : rating - $index >= 1, 'starhalf' : rating - $index > 0 && rating - $index < 1,  'starempty' : rating - $index <= 0 }\"></span></span>"
            },
            link: function(scope, element, attrs) {
                scope.starLength = _.fill(Array(parseInt(attrs.stars)), '*');
                //scope.rating = parseFloat(attrs.rating);
            }
        };
    }

    /** @ngInject */
    function CommonInfo($localStorage, $state, $mdToast) {
        return {
            getInfoObj: function() {
                return angular.copy($localStorage.fInfoObj);
            },
            getInfo: function(item) {
                // if (!$localStorage.fInfoObj || !$localStorage.fInfoObj.user)
                //     $state.go('main');
                // else
                if ($localStorage && $localStorage.fInfoObj)
                    return angular.copy($localStorage.fInfoObj[item]);
                else
                    return {};
            },
            setInfo: function(item, value) {
                var obj = $localStorage.fInfoObj || {};
                obj[item] = angular.copy(value);
                $localStorage.fInfoObj = obj;
            },
            reset: function() {
                $localStorage.$reset();
            },
            getAppUrl: function() {
                return 'http://onlinementors.in/apidott/v0';
            },
            getTestSeriesAppUrl: function() {
                return 'http://onlinementors.in/testSeriesApi';
            },
            showAlert: function(text) {
                $mdToast.show(
                    $mdToast.simple()
                    .textContent(text)
                    .hideDelay(3000)
                    .position('top right')
                );
            }
        };
    }
})();

(function() {
    'use strict';

    angular
        .module('flavido')
        .controller('AdminUserController', AdminUserController);

    /** @ngInject */
    function AdminUserController(CommonInfo, $http, growl, _, $log, $mdDialog, $scope) {
        var vm = this;

        vm.isCollapsed = true;
        vm.userListTab = 1;
        vm.userTab = 0;
        vm.users = [];
        vm.userList = [];
        vm.instructorTestmonial = {};

        vm.selectUserType = selectUserType;
        vm.addTestmonial = addTestmonial;
        vm.closeModal

        activate();

        function activate() {
            // getAllUsers();
            getUserType();
        }

        function getAllUsers() {
            $http.post(CommonInfo.getAppUrl() + "/getallusers", {}).then(
                function(response) {
                    if (response && response.data) {
                        if (response.data.status == 1) {
                            vm.users = response.data.data;
                            selectUserType(0, vm.userTypeList[0]);
                        } else if (response.data.status == 2) {
                            $log.log(response.data.message);
                        }
                    } else {
                        $log.log('There is some issue, please try after some time');
                    }
                },
                function(response) {
                    $log.log('There is some issue, please try after some time');
                }
            );
        }

        function getUserType() {
            $http.get(CommonInfo.getAppUrl() + "/getadmintypes").then(
                function(response) {
                    if (response && response.data) {
                        if (response.data.status == 1) {
                            vm.userTypeList = response.data.data;
                            getAllUsers();
                        } else if (response.data.status == 2) {
                            $log.log(response.data.message);
                        }
                    } else {
                        $log.log('There is some issue, please try after some time');
                    }
                },
                function(response) {
                    $log.log('There is some issue, please try after some time');
                }
            );
        }

        function selectUserType(index, type) {
            vm.userTab = index;
            vm.userType = type;
            vm.userList = _.filter(vm.users, { type: type.id });
            vm.userCount = {
                active: _.size(_.filter(vm.userList, { 'status': 1 })),
                inactive: _.size(_.filter(vm.userList, { 'status': 0 }))
            };
        }

        function addTestmonial(evt, instructor) {
            vm.instructorTestmonial = {
                instructorName: instructor.fullName,
                instructorId: instructor.id,
                added_by: CommonInfo.getInfo('userInfo').id
            };
            $mdDialog.show({
                targetEvent: evt,
                scope: $scope.$new(),
                templateUrl: 'app/admin/instructorTestmonial.tmpl.html',
                parent: angular.element(document.body),
                controller: DialogController
            });

            function DialogController($scope, $mdDialog, CommonInfo, $http, $log, Upload) {
                $scope.closeDialog = function() {
                    $mdDialog.hide();
                }

                $scope.addTestmonials = function(file) {
                    if (!file || angular.isString(file)) {
                        uploadTestmonials();
                    } else {
                        Upload.base64DataUrl(file).then(function(url) {
                            vm.instructorTestmonial.image = url;
                            uploadTestmonials();
                        });
                    }
                }

                function uploadTestmonials() {
                    $http.post(CommonInfo.getAppUrl() + "/createtestimonial", vm.instructorTestmonial).then(
                        function(response) {
                            if (response && response.data) {
                                if (response.data.status == 1) {
                                    growl.success('Testmonial Added successful');
                                    $mdDialog.hide();
                                } else if (response.data.status == 2) {
                                    $log.log(response.data.message);
                                }
                            } else {
                                $log.log('There is some issue, please try after some time');
                            }
                        },
                        function(response) {
                            $log.log('There is some issue, please try after some time');
                        }
                    );
                }
            }
        }
    }
})();

