$templateCache.put("app/main/aboutUs.html","<div ng-cloak><div class=site-wrap><section class=\"bg bg-navigation\" id=logged-out-header><div class=container><main-navbar></main-navbar></div></section><section class=\"home--learn courses__start\"><div class=container><h2>About Us</h2><p class=text-justify>Flavido is built around the idea that people should have the choice of availing high quality educational content and courses at the click of a button. Educational resources are available in geographical pockets with no easy means of accessing them apart from travelling. We aim at bridging this gap.<br>In this endeavor, our goal is to also lower the cost of education and provide you with new tools of learning.<br>Flavido ties up with education providers to create a digital library with meaningful resources which includes gists of important books, coaching compilations, digitized or handwritten notes.<br>Flavido sources study material from Educational providers, as well as students like you and provides them at the lowest cost possible.<br>Stay tuned to check out our offerings over next few weeks!<br><br></p><h5>Flavido Store For Educational Institutes</h5><p class=text-justify>In case you are an educational institute and would like to list your free or paid content on Flavido Store, please send us an email at cs@flavido.com<br><br></p><h5>Flavido Store for Students</h5><p class=text-justify>In case you are a student who wishes to provide his notes for free or paid, please send us an email at cs@flavido.com.<br></p></div></section></div><main-footer-bar></main-footer-bar></div>");
$templateCache.put("app/main/cancellationPolicy.html","<div ng-cloak><div class=site-wrap><section class=\"bg bg-navigation\" id=logged-out-header><div class=container><main-navbar></main-navbar></div></section><section class=\"home--learn courses__start\"><div class=container><h2>Cancellation Policy</h2><h5>Cancellation by flavido.com</h5><p class=text-justify>While we will make every effort to fulfill the orders you have placed, in certain cases Flavido.com reserves the right to cancel orders in the following cases:-<ul><li>When products goes out of stock with our supplier or seller or partner brand.</li><li>When there is limited Quantity available for Purchase.</li><li>When we have restrictions on the quantity or number or orders As per T&C.</li><li>When the address and/or contact details provided by you are inaccurate or insufficient.</li><li>When our delivery partners do not service your location. Currently we have limited supply in the North East and Jammu & Kashmir.</li></ul><br></p><h5>Cancellation by the Customer</h5><p class=text-justify>You as a customer can cancel your order anytime till the order has not been processed by us by calling our customer service. In such a case we will refund any payments already made by you for the order. We will not be able to cancel those orders that have already been processed and shipped by us. If we suspect any fraudulent transaction by any customer or any transaction which defies the terms & conditions of using the website, we at our sole discretion could cancel such orders. We will maintain a negative list of all fraudulent transactions and customers and would deny access to them or cancel any orders placed by them.<br><br></p><h5>Return & Refunds</h5><p class=text-justify>Orders once placed with us cannot be modified or clubbed with any other order. We use the best packaging methods for securing the orders from any damage as well as the services of the best shipping partners to ensure that the orders reach you in good condition. Though we make sure that the order is undamaged, we do not take any responsibility for the damages caused to your order while in transit. If you have received a damaged/ defective book or it does not comply with the specifications as per your original order, you are required to get in touch with us by dropping an email to support@flavido.com. If possible send us the image of the order as an attachment with the email. You can get your order replaced only once without any extra charge and we will send you the replacement only when we have received the book(s) back from you. We will take the returned product back with us and issue a credit note for the value of the return product(s). All the refunds either through cancellation or returns will be credited to your account on the Site. This can be used to pay your subsequent shopping bills. Please note that the shipping charges are non-refundable.<br></p></div></section></div><main-footer-bar></main-footer-bar></div>");
