$templateCache.put("app/main/faq.html","<div ng-cloak><div class=site-wrap><section class=\"bg bg-navigation\" id=logged-out-header><div class=container><main-navbar></main-navbar></div></section><section class=\"home--learn courses__start\"><div class=container><h2>Frequently Asked Questions</h2><h5>Q. I found the study material/online courses which I was looking for. What should I do now?</h5><p class=text-justify>If you have not registered with us, then please click on “Sign up” button on the top-right corner of the website. Once you have successfully registered, select the product you wish to buy & proceed to checkout. In case if you feel any difficulty to can contact us through phone at +91-9555923039 or send us an email to support@flavido.com. We will help you in placing your order.<br><br></p><h5>Q. Do you deliver across India or only in Delhi NCR?</h5><p class=text-justify>We do not have any geographical boundaries, we have multiple shipping partners, who ensure on-time delivery across the country.<br><br></p><h5>Q. I am trying to checkout but not getting option to select shipping method. What should I do now?</h5><p class=text-justify>Please don’t worry. Just share the PIN code (postal code) of your area where you want us to deliver your study material and we will add it in our database.<br><br></p><h5>Q. I am logged in but when I am trying to place the order it is showing invalid email/details. Why I am getting this error?</h5><p class=text-justify>You might have registered with us with social media account like Google or Facebook. Please use your Google or Facebook account to login. If still facing issue please contact our customer support team through phone at +91-9555923039 or send us an email to support@flavido.com .<br><br></p><h5>Q. How many times I can watch a lecture?</h5><p class=text-justify>There is no restriction in watching classes, all lectures can be watched unlimited times till your course subscription validity. Hence you don’t miss a class.<br><br></p><h5>Q. In lecture I am getting black screen & only able to listen to audio?</h5><p class=text-justify>Don’t worry. Just clear your browser(s) cookies & history from your PC/Mobile/Tablet/Laptop & then try. Follow the below mentioned instructions:</p><u>Google Chrome</u><br><ul><li>On your computer, open Chrome.</li><li>Hold Alt + F or At the top right, click More.</li><li>Click History > History.</li><li>On the left, click Clear browsing data. A box will appear.</li><li>From the drop-down menu, select how much history you want to delete. To clear everything, select the beginning of time.</li><li>Check the boxes for the info you want Chrome to clear, including “browsing history.” Click Clear browsing data.</li></ul>For more advanced Options visit: <a href=\"https://support.google.com/websearch/answer/465?source=gsearch&hl=en\" target=_blank>here</a><br><br><u>MOZILLA Firefox</u><br><ul><li>Click the menu button New Fx Menu , choose History, and then Clear Recent History….</li><li>Select how much history you want to clear</li><li>Click the drop-down menu next to Time range to clear to choose how much of your history Firefox will clear. History Win2</li><li>Next, click the arrow next to Details to select exactly what information will get cleared. Your choices are described in the What things are included in my history? section above.</li><li>History Win3 Fx11</li><li>Finally, click the Clear Now button. The window will close and the items you’ve selected will be cleared.</li></ul>For more advanced Options visit: <a href=https://support.mozilla.org/en-US/kb/delete-browsing-search-download-history-Firefox target=_blank>here</a><br><br><h5>Q. How do I submit my answer sheets/assignments for evaluation?</h5><p class=text-justify>You need to send your answer sheets/assignments scanned copies to testseries@flavido.com . Evaluated copies will be sent back to the students to their registered email.<br><br></p><h5>Q. I am enrolled in a course, I have some doubt related to a topic, how would I clear my doubts?</h5><p class=text-justify>Just like offline class student, you will also be able to interact with the faculty. We will share the contact details of the institute/faculty with whom you can clear your doubts.<br><br></p><h5>Q. Can I share my login credentials with my friend(s) or family members?</h5><p class=text-justify>We have a strict no-sharing arrangement. Much the same as in a physical classroom where you would buy a course for yourself alone, the courses we offer work the very same way. You may utilize various gadgets to take your course, We have tie-up with the students who work with us as well as our IT team keeps a strict cautiousness, and if any wrongdoing is discovered we make strict move – beginning with course subscription(s) cancellation, raising a police report and whatever else our lawful group regards fit.<br>Every video seen by an understudy is extraordinarily watermarked with distinguishing client information including client ID, IP, MAC address and so on at many places in both obvious and imperceptible arrangement. Along these lines, our tech group is viably ready to find clients that are mishandling the framework and in break of terms of the agreement.<br>We advise students not to be a part of any illegal activity as it will not only crush your dream to be in Government Jobs but also will affect your career in Private sector as well.<br><br></p><h5>Q. I am a student/faculty and want to sell my own prepared notes on your platform. Whom should I contact?</h5><p class=text-justify>We welcome student(s)/ Faculty member(s) to sell their own prepared notes to help other aspirants. You can send us your contact details to sales@flavido.com .<br><br></p><h5>Q. I don’t see my question answered here. What should I do now?</h5><p class=text-justify>Don’worry, we would be happy to resolve all your queries, You can contact us instantly through chat ( right-bottom corner of your browser), also you can contact us through phone at +91-9555923039 or send us an email to support@flavido.com .<br>Our customer service team is available from Monday – Saturday 9:00 AM – 8:00 PM, If you have contacted us during Off hours our will contact you on the very next working day.<br></p></div></section></div><main-footer-bar></main-footer-bar></div>");
